import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	data() {
		return {
			userData: {
				Age: "",
				BankCardNo: "",
				BankName: "",
				CreateDate: "",
				DateVerify: "",
				DescVerify: "",
				DueDate: "",
				FlagVerify: "",
				IDCardNo: "",
				IDCardPic: "",
				IDCardPicBack: "",
				MakerID: "",
				Name: "",
				NameVerifyManual: "",
				PhoneNumber: "",
				PicVerify: "",
				RelaDate: "",
				RunAddress: "",
				SelfDesc: "",
				SelfPic: "",
				Sex: "",
				ShopID: "",
				ShopURL: "",
				ShopUserName: "",
				ShopUserPWD: "",
				StatusVerify: "",
				SubBankName: "",
				WeChatID: "",
			}
		}
	},
	computed: mapState(['token']),
	onLoad() {
		// console.log(this.token)
		this.getUserData();
	},
	methods: {
		async getUserData() {
			var res = await this.$req.ajax({
				path: '/wxapi/member/Maker',
				title: '正在加载',
				data: {
					token: this.token

				}
			});
			if (res.data.code == 200) {
				this.userData = res.data.data;
			} else {
				this.$api.msg(res.data.message);
			}
		}
	}
}
